.coin__container {
  display: flex;
  justify-content: center;
}

.coin__row {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  height: 80px;
  border-bottom: 2px solid #b4e5f7;
  width: 900px;
}

.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}

.coin h1 {
  font-size: 16px;
  width: 150px;
}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.coin__symbol {
  text-transform: uppercase;
}

.coin__data {
  display: flex;
  text-align: right;
  justify-content: space-between;
  width: 100%;
}

.coin__price {
  width: 110px;
}

.coin__volume {
  width: 155px;
}

.coin__marketcap {
  width: 230px;
}

.coin__percent {
  width: 100px;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811;
}